"use client";

import { useState } from "react";
import Image from "next/image";
import { createPost } from "@/lib/api/createPostApi";

export default function CreatePostCard({ onSuccess }: { onSuccess: () => void }) {
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [photo, setPhoto] = useState<File | null>(null);
  const [preview, setPreview] = useState<string | null>(null);

  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  function handleImageUpload(e: React.ChangeEvent<HTMLInputElement>) {
    const file = e.target.files?.[0];
    if (!file) return;

    setPhoto(file);
    setPreview(URL.createObjectURL(file));
  }

  async function handleSubmit(e: React.FormEvent) {
    e.preventDefault();
    setError("");

    if (!name.trim() || !description.trim()) {
      setError("Both title and description are required.");
      return;
    }

    setLoading(true);
    const token = localStorage.getItem("accessToken") ?? "";

    const formData = new FormData();
    formData.append("name", name);
    formData.append("description", description);
    if (photo) formData.append("photo", photo);

    try {
      await createPost(token, formData);

      // Reset
      setName("");
      setDescription("");
      setPhoto(null);
      setPreview(null);

      onSuccess();
    } catch (err: any) {
      setError(err.message);
    }

    setLoading(false);
  }

  return (
    <div className="bg-white rounded-2xl shadow-md p-6 mb-8 border border-gray-200">
      <h2 className="text-xl font-bold text-[#1F1F1F] mb-3">Create a New Post</h2>

      {error && <p className="text-red-600 mb-3">{error}</p>}

      <form className="space-y-4" onSubmit={handleSubmit}>

        <div>
          <label className="text-sm font-semibold">Post Title</label>
          <input
            type="text"
            className="w-full mt-1 px-4 py-2.5 border rounded-xl"
            placeholder="Trending Winter Look"
            value={name}
            onChange={(e) => setName(e.target.value)}
          />
        </div>

        <div>
          <label className="text-sm font-semibold">Description</label>
          <textarea
            className="w-full mt-1 px-4 py-2.5 border rounded-xl resize-none"
            placeholder="Describe your look..."
            rows={3}
            value={description}
            onChange={(e) => setDescription(e.target.value)}
          />
        </div>

        <div>
          <label className="text-sm font-semibold">Upload Photo (optional)</label>
          <input type="file" accept="image/*" onChange={handleImageUpload} />

          {preview && (
            <div className="relative w-full h-48 mt-3 rounded-xl overflow-hidden border">
              <Image src={preview} fill alt="Preview" className="object-cover" />
            </div>
          )}
        </div>

        <button
          type="submit"
          disabled={loading}
          className="w-full py-3 bg-[#A27B5C] text-white rounded-xl text-lg font-semibold transition-all hover:bg-[#926B4B]"
        >
          {loading ? "Posting..." : "Post"}
        </button>
      </form>
    </div>
  );
}
