"use client";

import { useEffect, useState } from "react";
import Image from "next/image";
import Link from "next/link";
import { logoutUser } from "../utils/logout";
import { API_BASE_URL } from "@/config/apiDomain";
import { getAllPosts } from "../api/postsApi";
import { sendFeedback } from "../api/feedbackApi";
import CreatePostModal from "./CreatePostModal";

export default function HomeFeed() {
  const [user, setUser] = useState<any>(null);
  const [posts, setPosts] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);
  const [showCreateModal, setShowCreateModal] = useState(false);

  const [commentInput, setCommentInput] = useState<{
    [postId: string]: string;
  }>({});
  const [submittedFeedback, setSubmittedFeedback] = useState<{
    [postId: string]: boolean;
  }>({});

  // Load user + posts
  useEffect(() => {
    const userString = localStorage.getItem("user");
    const token = localStorage.getItem("accessToken");

    if (!userString || !token) {
      window.location.href = "/login";
      return;
    }

    const parsedUser = JSON.parse(userString);
    setUser(parsedUser);

    fetchPosts(token);
  }, []);

  async function fetchPosts(token: string) {
    try {
      const data = await getAllPosts(token);
      setPosts(data.posts);
    } catch (err: any) {
      console.error(err);
      logoutUser();
    }
    setLoading(false);
  }

  if (!user || loading) {
    return <div className="p-8 text-center text-xl">Loading posts...</div>;
  }

  const canShare = user.role === "Influencer";

  // LIKE logic
  async function handleLike(post: any) {
    try {
      const token = localStorage.getItem("accessToken") ?? "";

      const result = await sendFeedback(
        token,
        post.name,
        true,
        "" // no description for like
      );

      // Update likes count from backend
      setPosts((prev) =>
        prev.map((p) =>
          p.postId === post.postId
            ? { ...p, likesCount: result.postStats.likesCount }
            : p
        )
      );
    } catch (err: any) {
      alert(err.message);
    }
  }

  // COMMENT logic
  async function addComment(post: any, text: string) {
    if (!text.trim()) return;

    try {
      const token = localStorage.getItem("accessToken") ?? "";

      const result = await sendFeedback(token, post.name, false, text);

      const newFeedback = {
        id: result.feedback.feedbackId,
        userName: user.Name,
        comment: result.feedback.description,
      };

      setPosts((prev) =>
        prev.map((p) =>
          p.postId === post.postId ? { ...p, feedbacks: [newFeedback] } : p
        )
      );

      setSubmittedFeedback((prev) => ({
        ...prev,
        [post.postId]: true,
      }));
    } catch (err: any) {
      alert(err.message);
    }
  }

  return (
    <>
      <div className={showCreateModal ? "blur-sm pointer-events-none" : ""}>
        <div className="min-h-screen bg-[#F5F5F4] font-[Inter] px-4 py-6 relative">
          {/* Header */}
          <header className="flex justify-between items-center mb-8">
            <h1 className="text-3xl font-bold text-[#1F1F1F]">Pehnava Feed</h1>

            <button
              onClick={logoutUser}
              className="px-4 py-2 bg-[#111827] text-white rounded-lg hover:bg-black transition-all"
            >
              Logout
            </button>
          </header>

          {/* Posts List */}
          <div className="flex flex-col gap-8 max-w-xl mx-auto pb-24">
            {posts.map((p: any) => {
              const isRecommenderPost = p.creator.role === "Recommender";

              return (
                <div
                  key={p.postId}
                  className="bg-white rounded-2xl shadow-md overflow-hidden hover:shadow-xl transition-all"
                >
                  {/* IMAGE */}
                  <div className="relative w-full h-[360px]">
                    {p.photo ? (
                      <Image
                        src={`${API_BASE_URL}/${p.photo}`}
                        fill
                        alt="Post"
                        className="object-cover"
                      />
                    ) : (
                      <div className="bg-gray-300 w-full h-full flex items-center justify-center">
                        <span className="text-gray-600">No Image</span>
                      </div>
                    )}
                  </div>

                  {/* CONTENT */}
                  <div className="p-5 flex flex-col">
                    {/* CREATOR */}
                    <div className="flex items-center gap-3 mb-4">
                      <div className="w-12 h-12 bg-[#E8E5E1] rounded-full flex items-center justify-center text-xl font-bold">
                        {p.creator.Name.charAt(0)}
                      </div>

                      <div>
                        <p className="font-semibold text-[#111827]">
                          {p.creator.Name}
                        </p>
                        <p className="text-sm text-gray-500">
                          {p.creator.role}
                        </p>
                      </div>
                    </div>

                    {/* TITLE & DESCRIPTION */}
                    <h2 className="text-xl font-bold">{p.name}</h2>
                    <p className="text-sm text-gray-700 mt-1">
                      {p.description}
                    </p>

                    {/* LIKE + SHARE */}
                    <div className="flex justify-between items-center mt-4">
                      {/* LIKE (Disabled for Recommender posts) */}
                      {!isRecommenderPost ? (
                        <button
                          onClick={() => handleLike(p)}
                          className="text-[#A27B5C] font-semibold hover:text-[#926B4B]"
                        >
                          ðŸ¤Ž Like
                        </button>
                      ) : (
                        <span className="text-gray-400 text-sm">
                          Likes disabled
                        </span>
                      )}

                      {/* Likes Count */}
                      {!isRecommenderPost && (
                        <span className="text-sm text-gray-600">
                          {p.likesCount} Likes
                        </span>
                      )}

                      {/* SHARE (Influencer only) */}
                      {canShare && (
                        <button className="text-[#A27B5C] underline hover:text-[#926B4B]">
                          Share
                        </button>
                      )}
                    </div>

                    {/* COMMENTS SECTION */}
                    {!isRecommenderPost && (
                      <div className="mt-5">
                        <p className="font-semibold">Comments:</p>

                        {/* Existing Comments */}
                        <div className="mt-2 space-y-2 max-h-32 overflow-y-auto">
                          {(p.feedbacks || []).map((f: any) => (
                            <div
                              key={f.id}
                              className="bg-[#F7EDE2] p-2 rounded-lg text-sm"
                            >
                              <strong>{f.userName}: </strong>
                              {f.comment}
                            </div>
                          ))}
                        </div>

                        {/* COMMENT INPUT â€” hidden after submitting once */}
                        {!submittedFeedback[p.postId] && (
                          <div className="flex gap-2 mt-3">
                            <input
                              type="text"
                              placeholder="Add a comment..."
                              value={commentInput[p.postId] || ""}
                              onChange={(e) =>
                                setCommentInput({
                                  ...commentInput,
                                  [p.postId]: e.target.value,
                                })
                              }
                              className="flex-1 px-3 py-2 border rounded-lg text-sm"
                            />

                            <button
                              className="px-3 py-2 bg-[#111827] text-white rounded-lg"
                              onClick={() =>
                                addComment(p, commentInput[p.postId] || "")
                              }
                            >
                              Post
                            </button>
                          </div>
                        )}
                      </div>
                    )}
                  </div>
                </div>
              );
            })}
          </div>

          {/* Floating Create Post */}
          <button
            onClick={() => setShowCreateModal(true)}
            className="fixed bottom-6 right-6 bg-[#A27B5C] text-white px-6 py-4 rounded-full shadow-xl hover:bg-[#926B4B] transition-all"
          >
            + Create Post
          </button>
        </div>
      </div>
      {showCreateModal && (
        <CreatePostModal
          onClose={() => setShowCreateModal(false)}
          onSuccess={() =>
            fetchPosts(localStorage.getItem("accessToken") ?? "")
          }
        />
      )}
    </>
  );
}
